import { useState } from 'react';
import { app } from './base';

const db = app.firestore();

function ViewProducts() {
  const [users, setUsers] = useState([]);
  const [hello, setHello] = useState('');

  const searchSuk = (e) => {
    let hello = e.target.value.toString().toLowerCase();
    setHello(hello);
    console.log('hello');
  };

  const search = () => {
    db.collection('users')
      .where('skuNo', '==', hello)
      .limit(1)
      .onSnapshot((snapshot) => {
        const newUser = snapshot.docs.map((doc) => {
          return doc.data();
        });
        setUsers(newUser);
      });
  };

  // useEffect(() => {
  //   search();
  // }, []);

  const searchSukData = (e) => {
    e.preventDefault();
    search();
  };

  return (
    <div className='bg-white shadow-lg sm:rounded-3xl sm:p-20 mx-2'>
      <form>
        <div className='col-span-6'>
          <label
            htmlFor='item_name'
            className='block text-sm font-medium text-gray-700'
          >
            Search by Vendor Name
          </label>
          <input
            type='text'
            onChange={searchSuk}
            className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md mb-4'
          />
          <button
            className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mb-4'
            onClick={searchSukData}
          >
            Search
          </button>
        </div>
      </form>

      <div>
        <ul>
          {users.map((user) => {
            return (
              <li
                key={user.id}
                className='shadow sm:rounded-md sm:overflow-hidden'
              >
                <div className='grid grid-cols-2 gap-4 px-4 py-5 bg-white space-y-6 sm:p-6'>
                  <div className='col-span-1'>
                    <div className=''>
                      <label
                        htmlFor='item_name'
                        className='block text-sm font-medium text-gray-700'
                      >
                        Category
                      </label>
                      <input
                        type='text'
                        value={user.skuNo}
                        className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                      />
                    </div>
                    <div className=''>
                      <label
                        htmlFor='item_name'
                        className='block text-sm font-medium text-gray-700'
                      >
                        Description
                      </label>
                      <input
                        type='text'
                        value={user.description}
                        className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                      />
                    </div>
                    <div className=''>
                      <label
                        htmlFor='item_name'
                        className='block text-sm font-medium text-gray-700'
                      >
                        Brand Name
                      </label>
                      <input
                        type='text'
                        value={user.brand}
                        className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                      />
                    </div>
                    <div className=''>
                      <label
                        htmlFor='item_name'
                        className='block text-sm font-medium text-gray-700'
                      >
                        Vendor
                      </label>
                      <input
                        type='text'
                        value={user.vendor}
                        className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                      />
                    </div>
                  </div>
                  <div className='row-span-4 border-2 border-gray-200'>
                    <div className=''>
                      <img
                        className='object-fit: cover'
                        src={user.fileUrl}
                        alt={user.itemName}
                      />
                    </div>
                  </div>
                </div>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}

export default ViewProducts;
