import React from 'react';
import { app } from './base';
import swal from 'sweetalert';

const db = app.firestore();

function AddProducts() {
  const [fileUrl, setFileUrl] = React.useState(null);

  // const [itemName, setItemName] = React.useState([]);
  // const [description, setDescription] = React.useState([]);
  // const [brand, setBrand] = React.useState([]);
  // const [vendor, setVendor] = React.useState([]);

  const onFileChange = async (e) => {
    var file = e.target.files[0];
    // console.log(e.target.files);

    if (e.target.accept !== 'image/x-png') {
      window.alert('File does not support. You must use .png ');
      file = '';
      return false;
    }

    const storageRef = app.storage().ref();
    const fileRef = storageRef.child(file.name);
    await fileRef.put(file);
    setFileUrl(await fileRef.getDownloadURL());
  };

  const textInput = React.useRef();

  const clearInput = () => (textInput.current.value = '');

  const onSubmit = async (e) => {
    e.preventDefault();

    let skuNo = e.target.skuNo.value.toString().toLowerCase();
    let description = e.target.description.value.toString().toLowerCase();
    let brand = e.target.brand.value.toString().toLowerCase();
    let vendor = e.target.vendor.value.toString().toLowerCase();
    if (!skuNo || !fileUrl || !description || !brand || !vendor) {
      return;
    }
    await db.collection('users').doc(skuNo, description, brand, vendor).set({
      skuNo,
      description,
      fileUrl,
      brand,
      vendor,
    });
    console.log('uploaded...');
    clearInput();
    e.target.skuNo.value = '';
    e.target.description.value = '';
    e.target.brand.value = '';
    e.target.vendor.value = '';
    swal('file uploaded...');
    // textInput.current.value = '';
  };

  // console.log(users[0]);

  return (
    <>
      <div className='bg-white shadow-lg sm:rounded-3xl sm:p-20 mx-2'>
        <p className='text-lg text-gray-800 dark:text-gray-100 font-bold mb-4'>
          Add Products
        </p>
        <div className=''>
          <form onSubmit={onSubmit}>
            <div className='shadow sm:rounded-md sm:overflow-hidden'>
              <div className='px-4 py-5 bg-white space-y-6 sm:p-6'>
                {/* item Name */}
                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='item_name'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Category
                  </label>

                  <input
                    type='text'
                    required
                    name='skuNo'
                    className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                  />
                </div>

                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='description'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Item Description
                  </label>

                  <input
                    type='text'
                    required
                    name='description'
                    placeholder='Enter your Description'
                    className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                  />
                </div>

                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='brand'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Brand
                  </label>

                  <input
                    type='text'
                    name='brand'
                    required
                    placeholder='Enter your brand'
                    className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                  />
                </div>

                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='vendor'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Vendor Name
                  </label>

                  <input
                    type='text'
                    name='vendor'
                    required
                    placeholder='Enter your vendor'
                    className='mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md'
                  />
                </div>

                <div>
                  <label className='block text-sm font-medium text-gray-700'>
                    Image
                  </label>
                  <div className='mt-1 flex justify-center px-6 pt-5 pb-6 border-2 border-gray-300 border-dashed rounded-md'>
                    <div className='space-y-1 text-center'>
                      <svg
                        className='mx-auto h-12 w-12 text-gray-400'
                        stroke='currentColor'
                        fill='none'
                        viewBox='0 0 48 48'
                        aria-hidden='true'
                      >
                        <path
                          d='M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02'
                          strokeWidth={2}
                          strokeLinecap='round'
                          strokeLinejoin='round'
                        />
                      </svg>
                      <div className='flex text-sm text-gray-600'>
                        <label
                          htmlFor='file-upload'
                          className='relative cursor-pointer bg-white rounded-md font-medium text-indigo-600 hover:text-indigo-500 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-indigo-500'
                        >
                          <span>Upload a file</span>

                          <input
                            type='file'
                            onChange={onFileChange}
                            required
                            accept='image/x-png'
                            id='fileInput'
                            ref={textInput}
                            // className='hidden'
                          />
                        </label>
                        <p className='pl-1'>or drag and drop</p>
                      </div>
                      <p className='text-xs text-gray-500'>PNG up to 10MB</p>
                    </div>
                  </div>
                </div>
              </div>
              <div className='px-4 py-3 bg-gray-50 text-right sm:px-6'>
                <button
                  type='submit'
                  className='inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500'
                >
                  Submit
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}

export default AddProducts;
